.array-bar {
  background-color: #c2e8ce;
  display: inline-block;
  margin: 0 1px;
}

.margin-top-navbar {
  margin-top: 6rem !important;
}

.align-bars-center {
  text-align: center !important;
}
.navbar {
  background-color: #f2eee5 !important;
}

.navbar-function-link {
  text-decoration: none !important;
  text-align: center;
  color: #ffffff;
  transition: 0.3s;
  opacity: 0.6;
  background-color: #f6ad7b;
}

.navbar-function-link:hover {
  opacity: 1;
  color: #ffffff;
}
